<template>
    <div class="login box">
        <img src="../../static/image/back.jpg" alt="">
        <div class="login">
            <div class="login-title">
                <img src="../../static/image/logo.png" alt="">
                <p>!</p>
            </div>
            <div class="login_box">
                <div class="title">
                    <span @click="login_type=0"></span>
                    <span @click="login_type=1"></span>
                </div>
                <div class="inp" v-if="login_type==0">
                    <input type="text" placeholder=" / " class="user" v-model="username">
                    <input type="password" name="" class="pwd" placeholder="" v-model="password">
                    <div id="geetest1"></div>
                    <div class="rember">
                        <p>
                            <input type="checkbox" class="no" v-model="remember_me"/>
                            <span></span>
                        </p>
                        <p></p>
                    </div>
                    <button class="login_btn btn btn-primary" @click="get_captcha"></button>
                    <p class="go_login">
                        <router-link to="/register/"></router-link>
                    </p>
                </div>
                <div class="inp" v-show="login_type==1">
                    <input type="text" placeholder="" class="user">
                    <input type="text" class="pwd" placeholder="">
                    <button id="get_code" class="btn btn-primary"></button>
                    <button class="login_btn"></button>
                    <span class="go_login">
                    <router-link to="/register"></router-link>
                </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                username: "",
                password: "",
                remember_me: false,
                login_type: 0,
            }
        },
        methods: {
            //       
            get_captcha() {
                // API
                this.$axios({
                    url: this.$settings.HOST + "user/captcha/",
                    method: "get",
                    params: {
                        username: this.username,
                    }
                }).then(response => {
                    let data = JSON.parse(response.data);
                    // console.log(data, "222222");
                    // initGeetest
                    // 1
                    // 2appendTo
                    initGeetest({
                        gt: data.gt,
                        challenge: data.challenge,
                        product: "popup", // floatembedpopupPC
                        offline: !data.success, // 
                        new_captcha: data.new_captcha
                    }, this.handlerPopup);
                }).catch(error => {
                    console.log(error);
                })
            },
            //   
            handlerPopup(captchaObj) {
                console.log(captchaObj);
                let self = this;
                captchaObj.onSuccess(function () {
                    let validate = captchaObj.getValidate();
                    self.$axios({
                        url: self.$settings.HOST + "user/captcha/",
                        method: "post",
                        data: {
                            geetest_challenge: validate.geetest_challenge,
                            geetest_validate: validate.geetest_validate,
                            geetest_seccode: validate.geetest_seccode
                        },
                    }).then(response => {
                        console.log(response.data);
                        // 
                        self.user_login();
                    }).catch(error => {
                        console.log(error);
                    });
                });

                //   div
                document.getElementById("geetest1").innerHTML = "";
                // idcaptcha
                captchaObj.appendTo("#geetest1");
            },

            // 
            user_login() {
                this.$axios({
                    url: this.$settings.HOST + "user/login/",
                    method: 'post',
                    data: {
                        username: this.username,
                        password: this.password,
                    }
                }).then(response => {
                    //  True
                    localStorage.setItem("token", response.data.token)
                    if (this.remember_me) {
                        localStorage.user_token = response.data.token;
                        localStorage.username = response.data.username;
                        localStorage.user_id = response.data.user_id;
                    } else {
                        // false  
                        sessionStorage.user_token = response.data.token;
                    }
                    this.$message({
                        message: '',
                        type: 'success',
                        showClose: true,
                        duration: 1000,
                    });
                    // 
                    this.$router.push("/");
                }).catch(error => {
                    console.log(error);
                    this.$message.error('');
                })
            },
        }
    }
</script>

<style scoped>
    .box {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
    }

    .box img {
        width: 100%;
        min-height: 100%;
    }

    .box .login {
        position: absolute;
        width: 500px;
        height: 400px;
        top: 0;
        left: 0;
        margin: auto;
        right: 0;
        bottom: 0;
        top: -338px;
    }

    .login .login-title {
        width: 100%;
        text-align: center;
    }

    .login-title img {
        width: 190px;
        height: auto;
    }

    .login-title p {
        font-family: PingFangSC-Regular;
        font-size: 18px;
        color: #fff;
        letter-spacing: .29px;
        padding-top: 10px;
        padding-bottom: 50px;
    }

    .login_box {
        width: 400px;
        height: auto;
        background: #fff;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .5);
        border-radius: 4px;
        margin: 0 auto;
        padding-bottom: 40px;
    }

    .login_box .title {
        font-size: 20px;
        color: #9b9b9b;
        letter-spacing: .32px;
        border-bottom: 1px solid #e6e6e6;
        display: flex;
        justify-content: space-around;
        padding: 50px 60px 0 60px;
        margin-bottom: 20px;
        cursor: pointer;
    }

    .login_box .title span:nth-of-type(1) {
        color: #4a4a4a;
        border-bottom: 2px solid #84cc39;
    }

    .inp {
        width: 350px;
        margin: 0 auto;
    }

    .inp input {
        border: 0;
        outline: 0;
        width: 100%;
        height: 45px;
        border-radius: 4px;
        border: 1px solid #d9d9d9;
        text-indent: 20px;
        font-size: 14px;
        background: #fff !important;
    }

    .inp input.user {
        margin-bottom: 16px;
    }

    .inp .rember {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        margin-top: 10px;
    }

    .inp .rember p:first-of-type {
        font-size: 12px;
        color: #4a4a4a;
        letter-spacing: .19px;
        margin-left: 22px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
        align-items: center;
        /*position: relative;*/
    }

    .inp .rember p:nth-of-type(2) {
        font-size: 14px;
        color: #9b9b9b;
        letter-spacing: .19px;
        cursor: pointer;
    }

    .inp .rember input {
        outline: 0;
        width: 30px;
        height: 45px;
        border-radius: 4px;
        border: 1px solid #d9d9d9;
        text-indent: 20px;
        font-size: 14px;
        background: #fff !important;
    }

    .inp .rember p span {
        display: inline-block;
        font-size: 12px;
        width: 100px;
        /*position: absolute;*/
        /*left: 20px;*/

    }

    #geetest {
        margin-top: 20px;
    }

    .login_btn {
        width: 100%;
        height: 45px;
        background: #84cc39;
        border-radius: 5px;
        font-size: 16px;
        color: #fff;
        letter-spacing: .26px;
        margin-top: 30px;
    }

    .inp .go_login {
        text-align: center;
        font-size: 14px;
        color: #9b9b9b;
        letter-spacing: .26px;
        padding-top: 20px;
    }

    .inp .go_login span {
        color: #84cc39;
        cursor: pointer;
    }
</style>



